<template>
  <div class="pge">
    <div class="pge_header">
      <x-header :left-options="{showBack:true}">{{$route.meta.title}}</x-header>
    </div>
    <div class="pge_content">
      <group>
        <div class="head_portrait" v-on:click="Picture">
          <cell title="世峰头像" is-link>
						<span class="pge_pic">
							<img :src="portrait"/>
						</span>
          </cell>
        </div>
        <cell title="世峰昵称" :value="userInfo.name" is-link></cell>
        <popup-picker :title="title1" :data="list1" v-model="value1"></popup-picker>
        <datetime v-model="value2" title="我的生日"  confirm-text="确认" cancel-text="取消"  :min-year=1940 :max-year=2017></datetime>
        <cell title="我的收货地址" is-link></cell>
      </group>
      <actionsheet v-model="fileOK" :menus="fileMenus" @on-click-menu="changeFile" show-cancel cancel-text="取消"></actionsheet>
    </div>
  </div>
</template>

<script>
  import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

  import {getStorage,setStorage} from '../../common/js/storage'
  import {USERINFO_STORAGE,TIP_MESSAGE} from '../../common/public-variable'
  import {mapGetters} from 'vuex'
  export default {
    components: {
      PopupPicker,Actionsheet,XHeader,Cell,Group,XButton,Datetime
    },
    data() {
      return {
        userInfo:{},
        fileOK: false,
        fileMenus: {
          menu1: '拍照',
          menu2: '从相册选择'
        },
        title1: '性别',
        title2: '我的生日',
        list1: [['男','女','保密']],
        value1: ['男'],
        value2: '2015-11-12'

      }
    },
    computed:{
      ...mapGetters({
        currentDevice:'currentDevice'
      }),
      portrait:function() {
        if (this.userInfo && this.userInfo.portrait) {
          return this.userInfo.portrait;
        }
        else {
          return '../../static/image/touxiang.png';
        }
      }
    },
    methods: {
      Picture:function () {
        if (this.currentDevice.deviceName == 'wap') {
          this.$vux.toast.show({
            text: TIP_MESSAGE.changePhoto_Err,
            type: 'text',
            width: '20em'
          })
        } else {
          this.fileOK = true;
        }
        this.fileOK = true;
      },
      changeFile:function(key){
        if(key=='menu1'){//拍照
          //拍照
          //navigator.camera就是上面我们所说的clobbers定义的东西，用来调用插件中的方法的
          //getPicture就是插件中调用摄像头拍照的方法
          navigator.camera.getPicture(takeSuccess, takeFail, {
            quality : 75,
            destinationType : Camera.DestinationType.DATA_URL,
            sourceType : Camera.PictureSourceType.CAMERA,
            allowEdit : true,
            encodingType : Camera.EncodingType.JPEG,
            targetWidth : 100,
            targetHeight : 100
          });


        }else if(key=='menu2'){//从相册选择

        }
      },
      onChange (val) {
        console.log('val change', val)
      },
      onShow () {
        console.log('on show')
      },
      onHide (type) {
        console.log('on hide', type)
      }
    },
    created(){
      this.userInfo=getStorage(USERINFO_STORAGE);
    }

  }
  //拍照成功后回调
  function takeSuccess(imageURI) {
    alert('success' + imageURI);
  }

  //失败后回调
  function takeFail(message) {
    alert("拍照失败，原因：" + message);
  }
</script>

<style rel="stylesheet/less" lang="less">
  @import "../../assets/css/Pge.less";
  i {
    font-style: normal;
  }
</style>



