<template>
<div>
  <group>
  <x-input title="手机号" placeholder="请输入手机号" is-type="china-mobile" keyboard="number"  placeholder-align="left" @on-change="checkPhoneValid()" v-model="userInfo.userName" ref="phoneTxt"></x-input>
  <x-input title="发送验证码" placeholder="请输入验证码">
    <x-button slot="right" type="primary" :disabled="!phoneValid" mini @click.native="sendCode">发送验证码</x-button>
  </x-input>
  <x-button type="primary" style="margin-top:20px">下一步</x-button>
{{codeBtnText}}
  </group>
</div>
</template>

<script type="text/ecmascript-6">
  import {post} from '../../common/js/public-resource'
  import {sendCodeTime,CODE_TIME_STORAGE} from '../../common/public-variable'
  import {getStorage,setStorage} from '../../common/js/storage'
  import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'


  export default{
    data(){
      return {
        userInfo: {
          userName: '',
          passWord: ''
        },
        phoneValid: false,
        sendSecond: sendCodeTime
      }
    },
    components: {
      XInput, XButton, Group
    },
    computed: {
      codeBtnText: function () {
          if(this.sendSecond>0) {
            return '重新发送' + this.sendSecond + 's';
          }
          else{
            return '重新发送';
          }
      }
    },
    methods: {
      sendCode(){
        let that = this;
        /*    post('reg/notifyuser/mobileCode', {phone: this.userInfo.userName}, function (data, success) {
          if (success) {
            that.$vux.toast.show({
              text: '验证码发送成功',
              width:100
            })
            this.phoneValid=false;
          }
        })*/
    let intval=0;

        intval = setInterval(function () {

            let second=CalculationSendSec(that.sendSecond);
            if(second ==0){
              console.log(intval);
              console.log(that);
              clearInterval(intval);
              that.sendSecond=sendCodeTime;
            }
            else {
              that.sendSecond = sendSecond(that.sendSecond)
            }
        },1000);
      },
      checkPhoneValid(){
        this.phoneValid = this.$refs.phoneTxt.valid;
      }
    }
  }
  function CalculationSendSec(sec) {
    let secLocal = getStorage(CODE_TIME_STORAGE);
    if (!secLocal) {
      secLocal = sec;
    }
    secLocal--;
    setStorage(CODE_TIME_STORAGE, secLocal);
    return secLocal;
  }

</script>

<style>

</style>



