<template>
  <div>
    <x-header :left-options="{backText: '返回'}" >登录</x-header>
    <group>
      <x-input title="账号" placeholder="请输入账号" placeholder-align="left" v-model="userInfo.userName"></x-input>
      <x-input title="密码" placeholder="请输入密码" type="password" placeholder-align="left" v-model="userInfo.passWord" style="margin-bottom: 10px"></x-input>
      <x-button type="primary" @click.native="loginIn()" :disabled="isClick" style="margin-top:20px">登录</x-button>
      <x-button type="primary" @click.native="wechatLogin()" style="margin-top:20px">微信登录</x-button>
      <x-button type="primary" @click.native="qqLogin()" style="margin-top:20px">QQ登录</x-button>

    </group>
    <div style="margin-top:20px">
      <router-link to="/regiter" >新用户注册</router-link>
      <router-link to="/regiter" style="float:right">忘记密码</router-link>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import {post} from '../../common/js/public-resource'
import {USERINFO_STORAGE,TIP_MESSAGE} from '../../common/public-variable'
import {setStorage} from '../../common/js/storage'
import Confirm from 'vux/src/components/confirm/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import * as types from '../../store/mutation-types'
import {mapGetters} from 'vuex'
export default{
  data(){
    return {
      userInfo: {
        userName: "",
        passWord: "",
        type:1//用户登录类型，1账户密码，2微信，3新浪微博，4qq
      },
      isClick: true,
      show: false
    }
  },
  components: {
    Confirm, XInput, Group, XButton,XHeader
  },/*
  computed:{
    ...mapGetters({
      currentDevice:'currentDevice'
    })
  },*/
  methods: {
    loginIn(){

      var _this = this;
      post('login',
        {
          account: this.userInfo.userName.trim(),
          password: this.userInfo.passWord.trim()
        },
        function (data, success) {
          if (data && success) {
            alert(TIP_MESSAGE.login_success);
            let today = new Date()
            data["timeout"] = today.setDate(today.getDate() + 1);//一天过期
            data["password"] = _this.userInfo.passWord;
            setStorage(USERINFO_STORAGE, data);
            let query = _this.$route.query;
            if (query) {
              _this.$router.go({path: query.redirce})
            } else {
              _this.$router.go({path: '/member'})
            }
          } else {
            _this.$vux.toast.show({
              text: data,
              type: 'success'

            });
          }
        });
    },
    enableLoginBtn(){
      console.log(this.userInfo)
    },
    wechatLogin(){
      var scope = "snsapi_userinfo",
        state = "_" + (+new Date());
      Wechat.auth(scope, state, function (response) {
        // you may use response.code to get the access token.
        alert(JSON.stringify(response));
      }, function (reason) {
        alert("Failed: " + reason);
      });
    },
    qqLogin(){

    }

  }
  ,
  watch: {
    'userInfo': {
      handler: function (val, oldVal) {
        if (val.userName.trim().length > 0 && val.passWord.trim().length > 0) {
          this.isClick = false;
        }
        else {
          this.isClick = true;
        }
      },
      deep: true
    },
    deep: true
  },
  created(){
  }
}
</script>

<style>

</style>



