<template>
  <div class="set">
    <div class="set_header">
      <x-header :left-options="{showBack:true}" :right-options="{showMore: true}">{{$route.meta.title}}</x-header>
    </div>
    <div class="set_content">
      <group>
        <cell title="个人信息" is-link link="/member/person"></cell>
        <cell title="账户与安全" is-link></cell>
        <cell title="帮助中心" is-link></cell>
        <div class="wipe_cache" v-on:click="wipeCache">
          <cell title="清除本地缓存" >
            <span><i>{{cacheSize}}</i>KB</span>
          </cell>
        </div>

      </group>

      <group>
        <cell title="关于世峰户外" is-link></cell>
        <cell title="意见反馈" is-link></cell>
      </group>
      <button class="exit_btn" v-on:click="showPlugin">退出当前账户</button>
    </div>

  </div>
</template>


<script>
  import Confirm from 'vux/src/components/confirm/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

  import {setStorage,getStorage,removeStorage} from '../../common/js/storage'
  import {USERINFO_STORAGE} from '../../common/public-variable'
  export default {
    directives: {
      TransferDom
    },
    components: {
      Confirm,XHeader,Cell,Group,Badge,XButton
    },
    data () {
      return {
        show: false,
        cacheSize:(encodeURIComponent(JSON.stringify(localStorage)).length/1024).toFixed(2)
      }
    },
    methods: {
      showPlugin: function() {
          var that=this;
        this.$vux.confirm.show({
          content: '确定退出登录？',
          cancelText:'取消',
          confirmText:'确定',
          onConfirm () {
            removeStorage(USERINFO_STORAGE);
            that.$router.go({path:'/member'});
          }
        })
      },
      wipeCache:function(){
          var that=this;
        this.$vux.confirm.show({
          content: '确定清除本地缓存？',
          cancelText:'取消',
          confirmText:'确定',
          onConfirm () {
              that.cacheSize=0;
          }

        })
      }
    }
  }
</script>
<style rel="stylesheet/less" lang="less">
  @import "../../assets/css/set.less";
  i {
    font-style: normal;

  }
</style>



